  },
  methods:{
    demo:function(){
      this.$router.push('/bankmore');
    },
    demo2:function(){
      this.$router.push('/bankuser');
    }
  }
}
</script>

<style>
#bankOrganization{
  overflow: hidden;
}
#bankOrganization .agencyButton{
  margin-left: 10px;
  position: absolute;
  top: 20px;
